





























































import Vue from "vue";
import Component from "vue-class-component";
import { PostModel } from "@/x-vue/services/interfaces";
import { AppService } from "@/service/app.service";
import CommentFormComponent from "@/components/forum/CommentForm.vue";
import CommentViewComponent from "@/components/forum/CommentView.vue";
import PostMetaComponent from "@/components/forum/PostMeta.vue";
import VoteButtonsComponent from "@/components/forum/VoteButtons.vue";
import MineButtonsComponent from "@/components/forum/MineButtons.vue";
import UserDisplayName from "@/components/forum/UserDisplayName.vue";
import UserAvatar from "@/components/forum/UserAvatar.vue";
import FileDisplay from "@/components/forum/FileDisplay.vue";

@Component({
  props: ["post"],
  components: {
    CommentFormComponent,
    CommentViewComponent,
    PostMetaComponent,
    VoteButtonsComponent,
    MineButtonsComponent,
    UserDisplayName,
    UserAvatar,
    FileDisplay,
  },
}) // without this, Vue lifecycle methods will not be invoked
export default class PostViewComponent extends Vue {
  post!: PostModel;

  app = AppService.instance;

  async onClickDelete(): Promise<void> {
    const conf = confirm(
      `Are you sure you want to delete this post? [IDX]: ${this.post.idx}`
    );
    if (!conf) return;
    try {
      await this.app.api.postDelete(this.post?.idx);
      this.$emit("post-deleted", this.post?.idx);
    } catch (e) {
      this.app.error(e);
    }
  }

  onClickEdit(): void {
    // console.log("onEdit", `/edit/${this.post.idx}`);
    this.$router.push(`/edit/${this.post.idx}`).catch(() => null);
  }
}
